@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton {
    @include m.boton(v.$azul, v.$blanco);
    border-radius: 1rem;
}

.boton-cancelar {
    @include m.boton(v.$rojo, v.$blanco);
    border-radius: 1rem;
}

.boton-eliminar {
    @include m.boton(v.$rojo, v.$blanco);
    border-radius: 1rem;
}